"use client";

import { useEffect, useRef, useState } from "react";

export default function UndanganPage() {
  const audioRef = useRef<HTMLAudioElement>(null);
  const [musicStarted, setMusicStarted] = useState(false);

  const handlePlayMusic = () => {
    if (audioRef.current && !musicStarted) {
      audioRef.current
        .play()
        .then(() => {
          setMusicStarted(true);
        })
        .catch((err) => {
          console.log("Autoplay blocked:", err);
        });
    }
  };

  useEffect(() => {
    // Trigger music on first user interaction
    window.addEventListener("click", handlePlayMusic);
    return () => {
      window.removeEventListener("click", handlePlayMusic);
    };
  }, [musicStarted]);

  return (
    <main className="flex flex-col items-center justify-center min-h-screen p-6">
      <audio
        ref={audioRef}
        src="/music/Nadin Amizah - Berpayung Tuhan (Official Music Video) - Nadin Amizah.mp3"
        loop
      />

      <div className="text-center text-white">
        <h1 className="text-3xl font-bold mb-4">Selamat Datang di Undangan</h1>
        {!musicStarted && (
          <button
            onClick={handlePlayMusic}
            className="bg-pink-600 text-white px-4 py-2 rounded shadow mt-4"
          >
            Putar Musik
          </button>
        )}
      </div>
    </main>
  );
}
